import {Component} from 'react'
import DjRow from '../Molecules/Doujinshi/DjRow';
import HeaderPage from '../Molecules/Global/HeaderPage';
import axios from 'axios'
import laravel from '../../Servicios/laravel';
import { Link } from 'react-router-dom';

class Doujinshi extends Component {
    constructor(props) {
        super(props);

        this.state = {
            authors: []
        }

        this.onQuery = this.onQuery.bind(this)
    }

    componentDidMount(){
        this.init()
    }

    async init(){

        const {data} = await axios.get(laravel.makeUrl('/authors'))
            .then(res => res).catch(ex => console.log(ex))

        this.setState({authors: data.authors})
    }

    onQuery(e){

    }
    
    render() { 
        const {authors} = this.state
        
        return ( 
        <div>
            <div className="card mb-2 mt-2" >
                <div className="card-body">
                    <div className='row'> 
                        <div className="col-xl-2">
                            <b>Author list</b>
                        </div>       
                        <div className="col-xl-3 offset-xl-2">
                            <select name="f_type" id="" className="form-control me-2">
                                    <option value="1">Manga</option>
                                    <option value="1">Manga</option>
                                    <option value="1">Manga</option>
                                    <option value="1">Manga</option>
                            </select>
                        </div>
                        <div className='col-xl-3'>
                            <div className="input-group">
                                <input onKeyDown={this.onQuery} type="text" className="form-control" placeholder='Buscar ...' />
                                    <span className="input-group-text">
                                        <i className="bi bi-search"></i>
                                    </span>
                                </div>
                        </div>                
                        <div className='col-xl-2'>
                            <Link className='btn btn-primary' to="Agregar">
                                New Author
                            </Link>
                        </div>  
                    </div>
                </div>
            </div>
            <div className="row row-cols-3">
                {authors.map(author=>  
                    <div className="col" key={author.id}>   
                        <div className="card mb-2 text-center">
                            <div className="card-body">
                                <h5 className="card-title">{author.name}</h5>
                                <Link className="btn btn-outline-primary btn-sm me-2" to={`/dj/create/${author.id}`}>
                                    <i className="bi bi-pencil"></i>
                                </Link>
                                <button className="btn btn-outline-danger btn-sm">
                                    <i className="bi bi-trash"></i>
                                </button>
                            </div>
                        </div>

                    </div>
                    )}
            </div>
        </div>
         );
    }
}
 
export default Doujinshi;