import {Component} from 'react'
import DjWorkAdd from '../../Molecules/Doujinshi/DjWorkAdd';
import DjLinkManage from '../../Molecules/Doujinshi/DjLinkManage'

class DjWork extends Component {

    constructor(props) {
        super(props);
        
        this.state = {
            links: []
        }

        this.onPostSave = this.onPostSave.bind(this)
        
    }

    onPostSave(works){
        this.props.onPostSave({name: 'works', value: works})
    }

    render() { 
        const {work} = this.props
        const {links} = this.state
        return (
            <>
                <ul className="nav nav-tabs" id="myTab" role="tablist">
                    <li className="nav-item" role="presentation">
                        <button className="nav-link active" id="home-tab" data-bs-toggle="tab" data-bs-target="#home" type="button" role="tab" aria-controls="home" aria-selected="true">
                            Detail</button>
                    </li>
                    <li className="nav-item" role="presentation">
                        <button className="nav-link" id="profile-tab" data-bs-toggle="tab" data-bs-target="#profile" type="button" role="tab" aria-controls="profile" aria-selected="false">
                            Links</button>
                    </li>
                    <li className="nav-item" role="presentation">
                        <button className="nav-link" id="contact-tab" data-bs-toggle="tab" data-bs-target="#contact" type="button" role="tab" aria-controls="contact" aria-selected="false">
                            Character</button>
                    </li>
                    <li className="nav-item" role="presentation">
                        <button className="nav-link" id="contact-tab" data-bs-toggle="tab" data-bs-target="#contact" type="button" role="tab" aria-controls="contact" aria-selected="false">
                            Spoiler</button>
                    </li>
                </ul>
                <div className="tab-content mt-2" id="myTabContent">
                    <div className="tab-pane fade show active" id="home" role="tabpanel" aria-labelledby="home-tab">
                        <DjWorkAdd 
                            work={work} 
                            author={this.props.author}
                            postSave={this.onPostSave}
                            />
                    </div>
                    <div className="tab-pane fade" id="profile" role="tabpanel" aria-labelledby="profile-tab">
                        <DjLinkManage
                            work={work?.id} 
                            links={links}
                            postSave={(newlinks)=> this.setState({links:newlinks})}
                        />
                    </div>
                    <div className="tab-pane fade" id="contact" role="tabpanel" aria-labelledby="contact-tab">...</div>
                </div>

            </> 
        );
    }
}
 
export default DjWork;