import {Component} from 'react'
import Breacrumb from '../Molecules/Global/Breadcrumb';
import DjAuthor from '../Orgnisms/Doujinshi/DjAuthor';
import DjWorks from '../Orgnisms/Doujinshi/DjWorks';
import DjWork from '../Orgnisms/Doujinshi/DjWork';
import laravel from '../../Servicios/laravel';
import axios from 'axios';
import {setDoujin} from '../../Actions'
import {connect} from 'react-redux'
import ModalBootstrap from '../Molecules/Global/ModalBootstrap'
import {Modal} from 'bootstrap'



const mapStateToProps = state => ({
    doujin: state.doujin
})

const mapDispatchToProps = dispatch =>({
    setDoujin: value => dispatch(setDoujin(value))
})

class DjCreate extends Component {
    constructor(props) {
        super(props);

        this.state = {
            activeBody : "workAdd",
            author: null,
            works: [],
            workSelect: null
        }
        this.modal = null

        this.onPostSave = this.onPostSave.bind(this)
        this.onEditWork = this.onEditWork.bind(this)
    }

    async componentDidMount(){
        const {id} = this.props.match.params

        this.modal = new Modal("#dj_modal")

        if(id === undefined){
            return
        }

        const {author, works} = await axios.get(laravel.makeUrl(`/authors/${id}`))
            .then(res=>res.data)
        this.setState({
            author: author,
            works: works
        })

        this.props.setDoujin(author)
    }

    onPostSave(data){
        this.setState({[data.name]:data.value})
    }
    
    // Event Child
    onEditWork(work){
        this.setState({workSelect: work})
        this.modal.show()
    }

    render() { 
        const {activeBody, author, works, workSelect} = this.state 

        let nameBreacrumb = author? author.name : 'Create Doujinshi'

        return (             
            <>
                <div className="card mb-2 mt-2" >
                    <div className="card-body pb-0">
                        <div className='row'>                        
                            <div className='col-xl-6'>
                                <Breacrumb 
                                    ruta="/dj" 
                                    rutaName="Doujinshi" 
                                    actualName={nameBreacrumb}/>                                                        
                            </div>
                            <div className="col-xl-6">
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-xl-6">
                        <DjAuthor author={author} postSave={this.onPostSave}/>
                    </div>
                    <div className="col-xl-6">
                        <DjWorks works={works} editWork={this.onEditWork}/>
                    </div>
                    <ModalBootstrap activeBody={activeBody} title="Work "> 
                        <DjWork 
                            label="workAdd" 
                            author={author?.id} 
                            postSave={this.onPostSave}
                            work={workSelect}
                            />
                    </ModalBootstrap>
                </div>
            </>

         );
    }
}
 
export default connect(mapStateToProps, mapDispatchToProps)(DjCreate);