import React, { useState } from "react";
import TableBootrap from '../../Atomics/TableBootstrap'
import InputGroup from "../../Atomics/InputGroup";
import SelectLanguage from "../../Atomics/SelectLanguage";
import AsyncSelect from 'react-select/async'
import ScanlationApi from '../../../Servicios/ScanlationApi'
import DoujinApi from '../../../Servicios/DoujinApi'
import {now} from '../../../Funciones/global'
import MiniLoading from '../../Atomics/MiniLoaderButton'

function DjWorkManage(props) {

    const [state, setState] = useState({
        link: '',
        scanlation: '',
        language: 1,
        release: now()
    })

    const [loading, setLoading] = useState(false)

    const onSave = (e) => {
        e.preventDefault()
        
        setLoading(true)

        let data = new FormData(document.getElementById("form_link_work"))
        data.append("scanlation", state.scanlation)        

        DoujinApi.saveLink(props.work, data)
            .then(res => {
                props.postSave(res.data.links)
            })
            .catch(ex=>console.log(ex))
            .finally(()=> setLoading(false))
    }

    const loadOptions = (inputvalue, callback) => {

        if(inputvalue.length < 3){
            callback([])
            return
        }

        ScanlationApi.search(inputvalue)
            .then(res => {
                callback(res.data.scanlations)
            })
        
    }

    return ( 
        <>
            <form id="form_link_work" onSubmit={onSave}>
                <div className="row">
                    <div className="col-6">
                        <InputGroup label="Link">
                            <input type="text" className="form-control" name="link"/>
                        </InputGroup>
                    </div>
                    <div className="col-6">
                        <InputGroup label="Scanlation">
                        <div style={{width: '76%'}}>
                        <AsyncSelect loadOptions={loadOptions}
                                defaultOptions
                                cacheOptions
                                onChange={(value)=> setState({scanlation:value})}                                
                            />                            
                        </div>

                        </InputGroup>                        
                    </div>
                </div>
                <div className="row mt-2">
                    <div className="col-6">
                        <InputGroup label="Language">
                            <SelectLanguage/>
                        </InputGroup>
                    </div>
                    <div className="col-6">
                        <InputGroup label="Release">
                            <input type="text" 
                                name="language" 
                                className="form-control" 
                                defaultValue={state.release}/>
                        </InputGroup>                        
                    </div>
                </div>
                <div className="row mt-2">
                    <div className="col-2 offset-10 d-grid">
                        <button className="btn btn-sm btn-primary">
                            {loading? <MiniLoading/>: "Save"}                            
                        </button>
                    </div>
                </div>
            </form>
            <TableBootrap columns={['Link', 'Scanlation', 'Release', 'Language', 'Options']}>
                {props.links.map((link)=>
                    <tr>
                        <td>{link.link}</td>
                    </tr>
                )}
            </TableBootrap>
        </>
     );
}

export default DjWorkManage;