import laravel from "./laravel";
import config from "./BaseApi";

const api = {
    save: (data) => laravel.axiosInstance.post(
        '/comics',
        data,
        config
    ),
    update: (comic, data) => laravel.axiosInstance.post(
        `/comics/${comic}?_method=PUT`,
        data,
        config
    )
}

export default api
