import React ,{ useState, useEffect} from "react";
import Input from '../../Atomics/Input';
import global from '../../../Funciones/global';
import DoujinApi from '../../../Servicios/DoujinApi'
import alertify from 'alertifyjs'

function DjWorkAdd(props) {

    const [state, setState] = useState({
        name: '',
        release: global.now(),
        pages: 0,
        type: 1
    })

    const imagenDefault = "https://e.rpp-noticias.io/normal/2020/10/09/133613_1007593.jpg"

    useEffect(()=>{
        if(props.work){
            setState({
                name: props.work.name,
                release: props.work.release,
                pages: props.work.pages,
                type: props.work.type
            })
            document.getElementById('imagen').src = props.work.imagen
        }

    },[props.work])

    const onChange = (e) => {
        setState({[e.target.name]:e.target.value})
    }
    
    const onSave = async (e) => {
        e.preventDefault()

        let imagen = await global.getBlobImagen(document.getElementById('imagen').src)

        let formData = new FormData(document.getElementById("form_work_doujin"))

        formData.append('author', props.author)
        formData.append('imagen', imagen)

        DoujinApi.save(formData)
            .then(res => {
                alertify.alert('Work', res.data.message);
                this.props.postSave(res.data.works)
            })
            .catch(ex => console.log(ex))


    }

    const onCopy = async () => {

        document.getElementById('imagen').src = await global.copyImagen()
    }

    const limpier = () => {
        setState({
            name: '',
            release: global.now(),
            pages: 0,
            type: 1
        })
        document.getElementById('imagen').src = imagenDefault
    }

    return (  
        <form onSubmit={onSave} id="form_work_doujin">
            <div className="row">
                <div className="col-xl-8">
                    <Input label="Name">
                        <input name="name" onChange={onChange} value={state.name} type="text" className='form-control' />
                    </Input>
                    <Input label="Release" margin="true">
                        <input name="release" onChange={onChange} value={state.release} type="text" className='form-control' />
                    </Input>
                    <Input label="Pages" margin="true">
                        <input name="pages" onChange={onChange} value={state.pages} type="text" className='form-control' />
                    </Input>
                    <Input label="Type" margin="true">
                        <select name="type" onChange={onChange} value={state.type}  className="form-control">
                            <option value="1">Chapter</option>
                            <option value="2">Tantoubon</option>
                            <option value="3">History</option>
                        </select>
                    </Input>
                </div>
                <div className="col-xl-4">
                    <div className="text-center mb-2">
                        <img  alt="Imagen manga" id="imagen" className='img-thumbnail comic-img' 
                            src={imagenDefault}
                            height={500}
                        />
                    </div>                                    
                    <div className="row">
                        <div className="col-xl-6 d-grid">
                            <button type="button" onClick={onCopy} className='btn btn-secondary btn-sm'>
                                Copy
                            </button>
                        </div>
                        <div className="col-xl-6 d-grid">
                            <button type="button" className='btn btn-secondary btn-sm'>
                                Upload
                            </button>
                        </div>                                                                               
                    </div>
                </div>
            </div>

            <div className="d-grid gap-2 d-md-flex justify-content-md-end mt-2">
                <button  className="btn btn-default me-md-2 btn-sm" type="button">Algo</button>
                <button type="submit" className="btn btn-primary btn-sm" >Save</button>
            </div>
        </form> 
    );
}

export default DjWorkAdd;