import { Component } from 'react'
import alertify from 'alertifyjs'
import CardBootstrap from '../../Atomics/CardBootstrap'
import ComicApi from '../../../Servicios/ComicApi'
import MiniLoaderButton from '../../Atomics/MiniLoaderButton'

class MangaDetailCard extends Component {

    constructor(props) {
        super(props)
        this.state = {
            loading: false,
            madure: false,            
        }
        this.onGuardar = this.onGuardar.bind(this)
       
    }

    // Methods


    async update(comic, datos){
        
        if(datos.get("name") === comic.name){
            datos.delete("name")
        }

        return await ComicApi.update(comic.id, datos)
    }

    async onGuardar(e) {

        e.preventDefault()
               
        try {
            var datos = new FormData(document.getElementById('formManga'))

            datos.append('madure', this.state.madure? 1: 0)

            const {comic} = this.props

            const response = comic? await this.update(comic, datos) : await ComicApi.save(datos)
            console.log(response)
            const {data} = response

            if(response.status === 200){
                alertify.alert(data.message)

                this.props.onCreated(data.id, data.comic)
            }else{
                console.log(response.status)
            }
            
        } catch (error) {   
            
            console.log(error)
        }

    }

    componentDidMount(){
        if(this.props.comic){
            this.updateInterfaz()
        }
    }

    componentDidUpdate(prevProps) {
        // Uso tipico (no olvides de comparar las props):
        if (this.props.comic !== prevProps.comic) {
            this.updateInterfaz()
        }
    }

    updateInterfaz(){
        const {comic} = this.props

        document.getElementsByName('name')[0].value = comic?.name?? ''
        document.getElementsByName('date')[0].value = comic?.date??''
        document.getElementsByName('type')[0].value = comic?.type?? 1
        document.getElementsByName('source')[0].value = comic?.source??1

        this.setState({madure: comic?.madure === 1? true: false})

    }

    render() {
        const {comic} = this.props
        const {loading, madure} = this.state
        const nameButton = comic? "Update": "Save"
        return (
            <CardBootstrap>
                <form id="formManga" onSubmit={this.onGuardar}>
                    <div className="row">
                        <div className="col-xl-8">
                            <h5 className="card-title">Detail</h5>
                        </div>
                        <div className="col-xl-4">                                
                            <button className='btn btn-primary btn-sm me-2' disabled={loading}>
                                {loading === false? nameButton : <MiniLoaderButton/> }                                  
                            </button>
                            {comic?.exists_uuid == false && <button className="btn btn-primary btn-sm">Enable uuid</button>}
                        </div>
                    </div>

                    <div className="row mt-2">
                        <label className="col-xl-2 col-form-label"><small>Name</small> </label>
                        <div className="col-xl-10">
                            <input name="name" type="text" className='form-control form-control-sm' />
                        </div>
                    </div>
                    <div className="row mt-2">
                        <label className='col-xl-2 col-form-label'><small>Create date</small></label>
                        <div className="col-xl-4">
                            <input name="date" type="text" className='form-control form-control-sm' />
                        </div>
                        <label className="col-xl-2 col-form-label"><small>Status</small></label>
                        <div className="col-xl-4">
                            <select name="state" type="text" className="form-control form-control-sm">
                                <option value="1">Progreso</option>
                                <option value="2">Terminado</option>
                                <option value="3">Hiatus</option>
                            </select>
                        </div>
                    </div>
                    <div className="row mt-2">
                        <label className='col-xl-2 col-form-label'>
                            <small>Type</small>
                        </label>
                        <div className="col-xl-10">
                            <select name="type" id="" className="form-control form-control-sm">
                                <option value="1">Manga</option>
                                <option value="2">Manhua</option>
                                <option value="3">Manwhua</option>
                                <option value="4">Webtoon</option>
                            </select>
                        </div>
                    </div>
                    <div className="row mt-2">
                        <label className="col-xl-2 col-form-label"><small>+18</small></label>
                        <div className="col-xl-10">
                            <input type="checkbox" className='form-check-input' 
                                checked={madure}
                                onChange={(e)=> this.setState({madure:e.target.checked})}
                            />
                        </div>
                    </div>
                    <div className="row">
                        <label htmlFor="" className="col-xl-2 col-form-label"><small>Source</small></label>
                        <div className="col-xl-10">
                            <select name="source" id="" className="form-control form-control-sm">
                                <option value="1">Original</option>
                                <option value="2">Novel</option>
                            </select>
                        </div>
                    </div>
                </form>
            </CardBootstrap>
        );
    }
}


export default MangaDetailCard;